
// Screen-size breakpoints

@mixin for-phone-only {
    @media screen and (max-width: 599px) { @content; }
  }

@mixin for-tablet-portrait-up {
    @media screen and (min-width: 600px) { @content; }
  }

@mixin for-tablet-landscape-up {
    @media screen and (min-width: 900px) { @content; }
  }

@mixin for-desktop-up {
    @media screen and (min-width: 1200px) { @content; }
  }

@mixin for-big-desktop-up {
    @media screen and (min-width: 1800px) { @content; }
  }
// $breakpoints: (phone: 640px, 
//                tablet: 768px, 
//                desktop: 1024px) !default;
$my_breakpoints: (
  'phone': 320px,
  'tablet': 768px,
  'desktop': 1024px
) !default;



// 640px, 1024px, 1400px
$breakpoints-up: (
    "medium": "40em",
    "large": "64em",
    "xlarge": "87.5em");

// 639px, 1023px, 1399px
$breakpoints-down: (
    "small": "39.9375em",
    "medium": "63.9375em",
    "large": "87.4375em");

@mixin breakpoint-up($size){
    @media (min-width: map-get($breakpoints-up, $size)){
        @content;
    }
}

@mixin breakpoint-down($size){
    @media (max-width: map-get($breakpoints-down, $size)){
        @content;
    }
}

// Animations for the social media icons
@mixin animate-bouncey {
    transition: all 0.35s cubic-bezier(0.31, -0.205, 0.43, 1.8);
}

// Animations for the phone menu and hamburger.
@keyframes pulse {
    from {
        box-shadow: 0 0 0 0px rgba(255,255,255,0.6);
        background: rgba(255,255,255,0.6);
    }
    to {
        box-shadow: 0 0 0 1000px rgba(255,255,255,0);
        background: rgba(255,255,255,0.0);
    }
}

@keyframes openTop {
    0% {
        transform: translateY(-5px)
        rotate(0deg);
    }
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(90deg);
    }
}
@keyframes closedTop {
    0% {
        transform: translateY(-5px)
        rotate(0deg);
    }
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(90deg);
    }
}
@keyframes openMid {
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(45deg);
    }
}
@keyframes closedMid {
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(45deg);
    }
}
@keyframes openBtm {
    0% {
        transform: translateY(5px)
        rotate(0deg);
    }
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(90deg);
    }
}
@keyframes closedBtm {
    0% {
        transform: translateY(5px)
        rotate(0deg);
    }
    50% {
        transform: translateY(0px)
        rotate(0deg);
    }
    100% {
        transform: translateY(0px)
        rotate(90deg);
    }
}