.hero {
    $bg1: #dfd8c5;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    // padding: 0 10vw;
    // color: #ffffff;
    // position: relative;


    background-color: $bg1;
    // @media only screen and (max-aspect-ratio: 3/2) {
    //     background: 
    //         // url('../img/leaves.png') bottom left / contain no-repeat,
    //         url('../img/leaves.png') 60% 0%,
    //         // url('../img/mermaid.png') bottom -10vh left 65vw no-repeat;
    //         url('../img/mermaid.png') left 65vw bottom -10vmin / 100vh no-repeat;
    //     // background-size: contain;
    // }
    @include for-phone-only {
        background-image: url('../img/leaves.png'), url('../img/mermaid.png') ;
        background-position: left bottom, top 110% right -15vh;
        background-size: auto 45vh, auto 45vh;
        background-repeat: no-repeat, no-repeat;
    }
    @include for-tablet-portrait-up {
        background-image: url('../img/leaves.png'), url('../img/mermaid.png') ;
        background-position: left bottom, top 110% right -15vh;
        background-size: auto 60vh, auto 60vh;
        background-repeat: no-repeat, no-repeat;
    }
    @include for-tablet-landscape-up {
        background: 
        url('../img/leaves.png') 0 no-repeat,
        url('../img/mermaid.png') bottom -10vh right -33vmin no-repeat;
    background-size: contain;
    }
    h1 {
        position: relative;
        // font-size: 1rem;
        // font-size: 1em;
        font-size: calc(0.5vw + 0.5rem);
        text-transform: uppercase;
        text-align: center;

        img {
            margin: auto;
            display: block;
            width: calc(10vw + 100px);
            text-align: center;
        }

        &::after {
            content: 'Eindhoven';
            font-weight: 500;
            display: block;
            height: 100%;
            opacity: 0;
            transition: opacity ease-in-out 300ms; 
        }

        &:hover:after {
            opacity: 1;
        }
    }



}

